import React, { useState, useContext, useEffect } from 'react'
import Navbar from './Navbar'
import '../css/profile.css'
// import { useMatch, useParams } from 'react-router-dom'
import { useParams } from 'react-router-dom'
import axios from 'axios';
import Error from './Error.jsx'
import Footer from './Footer';
import loginServices from '../services/loginServices';
import { UserContext } from '../App';
import Dashboard from './Dashboard';
import RenderUserPosts from './RenderUserPosts';
const postUrl = '/api/users'


const Profile = () => {
  const [user, getUser] = useState([])
  const [glbUser, setUser] = useContext(UserContext)
  // const userMatch = useParams().profile
  // const match = useMatch('/profile/:profile')
  // const userMatch = match.params.profile
  // console.log("usematchis", userMatch)
  const userParams = useParams().profile
  // console.log("userParams is", userParams)
  useEffect(() => {
    async function fetchGlbUser() {
      const loggedInUser = window.localStorage.getItem('userToken')
      if (loggedInUser) {
        const user = JSON.parse(loggedInUser)
        loginServices.setToken(user.token)
        // console.log("Profile token is ", user.token)
        // loginServices.createToken()
        const config = { headers: { Authorization: `bearer ${user.token}` } }
        const getParamsUser = await axios.get(`${postUrl}/${userParams}`, config)
        setUser(getParamsUser.data.glbUserToken)
        getUser(getParamsUser.data.newUser)
        // console.log(getParamsUser.data.newUser)
        return getParamsUser.data.glbUserToken
      }
    }
    fetchGlbUser()
  }, [setUser, userParams])

  if (!user) {
    return <Error />
  }

  return (
    <>
      <Navbar />
      <div className='container'>
        <Dashboard user={user} glbUser={glbUser} setUser={getUser} />

        <h1 style={{ textAlign: 'center', margin: '4vh' }} >Blogs</h1>

        {
          user.blog && user.blog.length ?
            <div className='blogs'>
              <RenderUserPosts user={user} />
            </div>
            : <h1>No blogs</h1>
        }
      </div>
      <Footer />
    </>
  )
}

export default Profile
