import React from 'react'

import { Table, TableBody, TableContainer, TableCell, TableRow } from '@mui/material';
import AddBlog from './AddBlog';

const tableData = (format, data) => {
  return { format, data }
}

// console.log("User blog is", user.blog[0].title)

const Dashboard = ({ user, glbUser, setUser }) => {

  const row = [
    tableData("Name", `${user.fname} ${user.lname}`),
    tableData("Date Of Birth", `${user.dob}`),
    tableData("Gender", `${user.gender}`),
    tableData("Username", `${user.username}`),
    tableData("No of Blogs", user.blog ? `${user.blog.length}` : `N/A`),
  ]
  return (
    <>
      <h1
        style={{
          textAlign: 'center',
          textDecoration: 'underline',
          padding: '2px 0 25px 0'
        }}
      >  {user.fname} {user.lname} Profile</h1>
      <div className='containerFlex'>
        <div className='profile'>
          {/* <Avatar>M</Avatar> */}
        </div>
        <div className='data'>
          <TableContainer>
            <Table>
              <TableBody>
                {
                  row.map((data) => (
                    <TableRow key={data.format} >
                      <TableCell component="th" scope="row"><h3>{data.format}</h3></TableCell>
                      <TableCell component="th" scope="row">{data.data}</TableCell>
                    </TableRow>
                  ))
                }
              </TableBody>
            </Table>
          </TableContainer>
        </div>
        {/* <div className='last'> */}
        {/* <AddBlog /> */}
        {/* </div> */}
        {
          glbUser.id === user.id && <div>
            <AddBlog user={user} setUser={setUser} />
          </div>
        }
      </div>

    </>
  )
}

export default Dashboard
