import { Button, TextField } from '@mui/material'
import React, { useState } from 'react'
import axios from 'axios'

const AddBlog = ({ user, setUser }) => {

  async function postBlog() {
    const newPost = {
      title,
      blog,
      author: user.username
    }
    try {
      const blog = await axios.post(`/api/blogs/`, newPost)
      console.log(blog.data)
      setUser(user.blog.concat(blog.data))
      setTitle('')
      setBlog('')
    } catch (err) {
      console.log(err)
    }
  }

  const [title, setTitle] = useState('')
  const [blog, setBlog] = useState('')

  return (
    <>
      <h2 style={{ textAlign: 'center', textDecoration: 'underline', marginBottom: '3vh' }}>Add a blog</h2>
      <TextField required name="title" value={title} onChange={(e) => setTitle(e.target.value)} label="Title" variant='standard' /> <br />
      <TextField required name="title" value={blog} onChange={(e) => setBlog(e.target.value)} label="Blog" multiline rows={4} variant='standard' /> <br />
      <br />
      <Button onClick={postBlog} color='success' variant='contained'>Post</Button>
    </>
  )
}

export default AddBlog
