import { useContext, useReducer, useState } from 'react'
import TextField from '@mui/material/TextField'
import { Button, FormControlLabel, Radio, RadioGroup } from '@mui/material'
import '../css/form.css'
import { Link } from 'react-router-dom'
import { Box } from '@mui/system'
import Visibility from '@mui/icons-material/Visibility'
import VisibilityOff from '@mui/icons-material/VisibilityOff'
import Input from '@mui/material/Input'
import InputAdornment from '@mui/material/InputAdornment'
import IconButton from '@mui/material/IconButton'
import Navbar from './Navbar'
import { reducer, initialState } from '../hooks/UseReducer'
import loginServices from '../services/loginServices'
import { UserContext } from '../App'

const SignIn = () => {
  const [showPassword, setShowPassword] = useState(false);
  const [user, setUser] = useContext(UserContext)
  // const setUser = useContext(UserContext)
  // const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleClickShowPassword = () => setShowPassword(!showPassword);

  const [state, dispatch] = useReducer(reducer, initialState)


  const handleOnChange = (e) => {
    dispatch({ type: e.target.name, payload: e.target.value })
  }
  const handleSignIn = async () => {
    const newUser = {
      fname: state.fname,
      lname: state.lname,
      username: state.username,
      email: state.email,
      gender: state.gender,
      dob: state.dob,
      password: state.password
    }
    const user = await loginServices.signIn(newUser)
    loginServices.setToken(user.token)
    const glbUser = await loginServices.createToken()
    // console.log(user)
    // console.log(user.token)
    // console.log("Glbuer is this ", glbUser)
    setUser(glbUser)
    window.localStorage.setItem('userToken', JSON.stringify(user))
    // await loginServices.postData(newUser)
    dispatch({ type: 'RESET' })
  }

  return (
    <>
      <Navbar />
      <Box>
        <div className='form'>
          <form>
            <h1>Sign Up Form</h1>
            <TextField required name="fname" value={state.fname} onChange={handleOnChange} label="First Name" variant='standard' /> <br />
            <TextField required name='lname' value={state.lname} onChange={handleOnChange} label="Last Name" variant='standard' /> <br />
            <TextField required name='username' value={state.username} onChange={handleOnChange} label="Username" variant='standard' /> <br />
            <TextField required name='email' value={state.email} onChange={handleOnChange} type='email' label="Email" variant='standard' /> <br />
            <br />
            <RadioGroup row name='RadioGroup' >
              <FormControlLabel name="gender" value='male' checked={state.gender === 'male'} onChange={handleOnChange} control={<Radio />} label='Male' />
              <FormControlLabel name="gender" value='female' checked={state.gender === 'female'} onChange={handleOnChange} control={<Radio />} label='Female' />
              <FormControlLabel name="gender" value='others' checked={state.gender === 'others'} onChange={handleOnChange} control={<Radio />} label='Others' />
            </RadioGroup>
            <Input
              name="password"
              label="Password"
              value={state.password}
              onChange={handleOnChange}
              id="standard-adornment-password"
              type={showPassword ? 'text' : 'password'}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                  >
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              }
            />
            <br />
            <br />
            <TextField
              name="dob"
              value={state.dob}
              onChange={handleOnChange}
              id="date"
              label="Date Of Birth"
              type="date"
              sx={{ width: 220 }}
              InputLabelProps={{
                shrink: true,
              }}
            />
            <br />
            <br />
            <Button variant='contained' onClick={handleSignIn} color='success'>Sign Up</Button>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <Link to='/login'>
              <Button variant='outlined' color='success'>Log In</Button>
            </Link>

          </form>
        </div>
      </Box>

    </>
  )
}

export default SignIn
