import React, { useContext, useEffect, useState } from 'react'
import { UserContext } from '../App'
import loginServices from '../services/loginServices'
import Navbar from './Navbar'
import { Typography, CardActions, Card, CardHeader, Avatar, IconButton, CardContent, } from '@mui/material'
import { red } from '@mui/material/colors';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import FavoriteIcon from '@mui/icons-material/Favorite';
import { Link } from 'react-router-dom'
import Footer from './Footer'
const Home = () => {

  const [user, setUser] = useContext(UserContext)
  const [blogs, setBlogs] = useState([])
  useEffect(() => {
    async function fetchAll() {
      const loggedInUser = window.localStorage.getItem('userToken')
      if (loggedInUser) {
        const user = JSON.parse(loggedInUser)
        // setUser(user)
        loginServices.setToken(user.token)
        const glbUser = await loginServices.getAllUser()
        // console.log(glbUser.getUser)
        setUser(glbUser.getGlobalUser)

        const blogs = await loginServices.getAllBlog()
        setBlogs(blogs)
        console.log(blogs)
      }
    }
    fetchAll()
  }, [setUser])
  return (
    <>
      <Navbar />
      {/* <h1>Home Page</h1> */}
      {/* {user ? <h1>GloblaUseris {user.username} {user.dob}</h1> : <h1>Please Log In</h1>} */}
      {/* {user && user.dob} */}

      {
        blogs.map(blog =>
          <Card sx={{ margin: '3vh' }} key={blog.id}>
            <CardHeader
              avatar={
                <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                  R
                </Avatar>
              }
              action={
                <IconButton aria-label="settings">
                  <MoreVertIcon />
                </IconButton>
              }
              title={<Link to={`/profile/${blog.author.username}`} > {blog.author.username}</Link>}
              subheader="September 14, 2016"
            />
            <CardContent>
              <Typography variant="body1" color="text.primary">
                {blog.title}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {blog.blog}
              </Typography>
            </CardContent>
            <CardActions disableSpacing>
              <IconButton onClick={() => { "haha" }} aria-label="add to favorites">
                <FavoriteIcon sx={{ color: red[500] }} />
              </IconButton>
            </CardActions>
          </Card>
        )
      }
      <Footer />
    </>
  )
}

export default Home
