import axios from "axios";
const postUrl = '/api/users'
const blogUrl = '/api/blogs'

let token = null;
let config;

const setToken = newToken => {
  token = `bearer ${newToken}`
  // console.log("Token is", token)
}

const signIn = async newObj => {
  const res = await axios.post(`${postUrl}`, newObj)
  return res.data
}

const logIn = async newObj => {
  const res = await axios.post(`${postUrl}/login`, newObj)
  return res.data
}


const getAllUser = async () => {
  const loginInUser = {
    headers: { Authorization: token }
  }
  const data = await axios.get(`${postUrl}`, loginInUser)
  return data.data
}

const getAllBlog = async () => {
  const res = await axios.get(blogUrl)
  return res.data
}

const getToken = async (newObj) => {
  // const loginPage = {
  // headers: { Authorization: token }
  // }
  const res = await axios.post(`${postUrl}/login`, newObj)
  console.log(res.data)
  return res.data
}

const createToken = async () => {
  config = {
    headers: { Authorization: token }
  }
  const res = await axios.get(`${postUrl}`, config)
  // console.log("data after token", res.data)
  return res.data
}

const loginServices = { getAllBlog, logIn, getToken, getAllUser, signIn, setToken, createToken }

export default loginServices
