import React, { useEffect, useState } from 'react'
import '../css/form.css'
import Visibility from '@mui/icons-material/Visibility'
import VisibilityOff from '@mui/icons-material/VisibilityOff'
import Input from '@mui/material/Input'
import InputAdornment from '@mui/material/InputAdornment'
import IconButton from '@mui/material/IconButton'
import Navbar from './Navbar'
import TextField from '@mui/material/TextField'
import { Button } from '@mui/material'
import { Link, useNavigate } from 'react-router-dom'
// import axios from 'axios'
import loginServices from '../services/loginServices'
// import { initialState } from '../hooks/UseReducer'

const Login = ({ setUser }) => {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [showPassword, setShowPassword] = useState(false);

  // const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleClickShowPassword = () => setShowPassword(!showPassword);
  const navigate = useNavigate()
  const handleLogIn = async () => {
    // console.log('btn clicked')
    const newUser = {
      email,
      password
    }

    try {
      const user = await loginServices.logIn(newUser)
      loginServices.setToken(user.token)
      setUser(user)
      window.localStorage.setItem('userToken', JSON.stringify(user))
      navigate(`/profile/${user.username}`)
    } catch (error) {
      console.log(error)
    }

  }
  // console.log("User is", user)
  return (
    <>
      <Navbar />
      {/* <h1>{user.lname}</h1> */}

      <div className='form'>
        <form >
          <h1>Login Page</h1>
          <TextField
            required
            name='email'
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type='email'
            label="Email"
            variant='standard' />
          <br />
          <Input
            name="password"
            label="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            id="standard-adornment-password"
            type={showPassword ? 'text' : 'password'}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                >
                  {showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
          />
          <br />
          <br />
          <Button variant='contained' onClick={handleLogIn} color='success'>Log In</Button>
          &nbsp;&nbsp;&nbsp;&nbsp;
          <Link to='/signup'>
            <Button variant='outlined' color='success'>Sign Up</Button>
          </Link>
        </form>
      </div>
    </>
  )
}

export default Login
